// Libs
import { colorsNamed } from 'culori';
import { useState } from 'react';

// Data
import coatedFogra from '../data/cmyk-adobe-coated-fogra39.json';
import uncoatedFogra from '../data/cmyk-adobe-uncoated-fogra29.json';
import webCoated from '../data/cmyk-adobe-us-web-coated-swop.json';
import webUncoated from '../data/cmyk-adobe-us-web-uncoated.json';


import Swatch from '../components/Swatch';

import './index.css';

const colors = Object.keys(colorsNamed).sort();

const Home = () => {

	let [tab, setTab] = useState('rgb');

	return <div className='swatches'>
		<div className={`swatchlist ${ tab === 'rgb' ? "swatchlist--active" : ""}`}>{colors.map((color, i) => <Swatch color={color} key={i}/>)}</div>
		<div className={`swatchlist ${ tab === 'coatedFogra' ? "swatchlist--active" : ""}`}>{coatedFogra.map(({ converted }, i) => <Swatch color={converted} key={i}/>)}</div>
		<div className={`swatchlist ${ tab === 'uncoatedFogra' ? "swatchlist--active" : ""}`}>{uncoatedFogra.map(({ converted }, i) => <Swatch color={converted} key={i}/>)}</div>
		<div className={`swatchlist ${ tab === 'rgb' ? "swatchlist--active" : ""}`}>{webCoated.map(({ converted }, i) => <Swatch color={converted} key={i}/>)}</div>
		<div className={`swatchlist ${ tab === 'rgb' ? "swatchlist--active" : ""}`}>{webUncoated.map(({ converted }, i) => <Swatch color={converted} key={i}/>)}</div>
	</div>
};

export default Home;
